.contenedor {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;

  // border: 1px solid red;

  & h1 {
    font-size: 20px;
    color: #4A72B2;
    font-family: "Roboto";
    text-decoration: underline;
  }

  & h3 {
    font-size: 70px;
  }

  & p {
    color: #444242;
  }
}

.contents {
  h3 {
    font-size: 45px;
    color: #4A72B2;
    background: #ffffff;
    text-shadow: 1px 0px 1px #cccccc, 0px 1px 1px #eeeeee, 2px 1px 1px #cccccc,
      1px 2px 1px #eeeeee, 3px 2px 1px #cccccc, 2px 3px 1px #eeeeee,
      4px 3px 1px #cccccc, 3px 4px 1px #eeeeee, 5px 4px 1px #cccccc,
      4px 5px 1px #eeeeee, 6px 5px 1px #cccccc, 5px 6px 1px #eeeeee,
      7px 6px 1px #cccccc;

  }

}

.cabecera-portada{

    h1{

        color: #4A72B2;
        margin-top: 80px;
        margin-bottom: 60px;
        font-size: 50px;
    }

    h2{
        color: #444242;
        margin-bottom: 80px;
        
    }
    
    img{
        margin-bottom: 150px;
    }
}
